export const App = () => {
  const [mode, setMode] = React.useState("light");
  const colorMode = React.useMemo(
    () => ({
      toggleColorMode: () => {
        setMode((prevMode) => (prevMode === "light" ? "dark" : "light"));
      },
    }),
    []
  );

  const theme = React.useMemo(
    () =>
      createTheme({
        palette: {
          mode,
        },
      }),
    [mode]
  );
  return (
    <ColorModeContext.Provider value={colorMode}>
      <ThemeProvider theme={theme}>
        <Router>
          <AuthProvider>
            <Layout>
              <Box
                sx={{
                  display: "flex",
                  width: "100%",
                  alignItems: "center",
                  justifyContent: "center",
                  bgcolor: "background.default",
                  color: "text.primary",
                  borderRadius: 1,
                  p: 3,
                }}
              >
                {theme.palette.mode} mode
                <IconButton
                  sx={{ ml: 1 }}
                  onClick={colorMode.toggleColorMode}
                  color="inherit"
                >
                  {theme.palette.mode === "dark" ? (
                    <Brightness7Icon />
                  ) : (
                    <Brightness4Icon />
                  )}
                </IconButton>
              </Box>
              <Switch>
                <PrivateRoute exact path="/" component={Home} />
                <PrivateRoute path={ROUTES.HOME} component={Home} />
                <PrivateRoute path={ROUTES.PROFILE} component={Profile} />
                <PrivateRoute
                  path={ROUTES.UPDATE_PROFILE}
                  component={UpdateProfile}
                />
                <PrivateRoute path={ROUTES.CREATE_AD} component={CreateAd} />
                <PrivateRoute path={ROUTES.MY_ADS} component={MyAds} />
                <PrivateRoute path={ROUTES.EDIT_AD} component={EditAd} />
                <PrivateRoute path={ROUTES.AD_DETAILS} component={AdDetails} />

                <Route path={ROUTES.ABOUT} component={About} />
                <Route path={ROUTES.LOG_IN} component={Login} />
                <Route path={ROUTES.SIGN_UP} component={Signup} />
                <Route
                  path={ROUTES.FORGOT_PASSWORD}
                  component={ForgotPassword}
                />
              </Switch>
            </Layout>
          </AuthProvider>
        </Router>
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
};
